          if (key === 'vehicleDate') {
            this.sellerForm[key] = moment(resp.data[key]).format("YYYY-MM-DD")
          } else {
            this.allForm[key] = resp.data[key]
            this.buyerForm[key] = resp.data[key]
            this.sellerForm[key] = resp.data[key]
          }
        }
        this.pretrialData = Object.assign({}, this.allForm, this.buyerForm, this.sellerForm)
      });
